@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
}

.app_container {
  width: 100%;
  height: 100%;
  background-color: #e7e7e7;
}

.about_container {
  width: 80%;
  margin: 0 auto;
  padding: 20px 0;
  animation: slide_about 1s;
  background-color: #e7e7e7;
}

@keyframes slide_about {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.about_title {
  font-family: "Ubuntu", sans-serif;
  display: block;
  font-size: 32px;
  font-style: italic;
  margin-bottom: 25px;
  color: #133b5c;
}

.about_info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Open Sans", sans-serif;
}

.about_info img {
  width: 25%;
  height: 25%;
  border-radius: 50%;
  margin-left: 5%;
}

.about_content {
  margin: 0 5%;
}

.about_skill {
  font-family: "Ubuntu", sans-serif;
}

.skill_title {
  display: block;
  font-size: 32px;
  font-style: italic;
  margin-bottom: 25px;
  color: #133b5c;
}

.skill_front {
  border-top: 2px #1e5f74 solid;
  margin-left: 10px;
}

.front_title {
  position: relative;
  color: #1e5f74;
  top: -14px;
  padding-right: 10px;
  font-size: 20px;
  background-color: #e7e7e7;
}

.front_skills {
  display: flex;
  text-align: center;
  font-family: "Open Sans", sans-serif;
}

.front_skills div {
  margin-left: 20px;
}

.front_skills span {
  margin-left: 20px;
}

.html_logo {
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100px;
  height: 120px;
}

.css_logo {
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100px;
  height: 120px;
}

.javascript_logo {
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100px;
  height: 120px;
}

.react_logo {
  background-image: url(/portfolio/static/media/react.d19e43ce.png);
  background-size: 100%;
  background-repeat: no-repeat;
  margin-top: 10px;
  width: 100px;
  height: 110px;
}

.skill_back {
  border-top: 2px #1e5f74 solid;
  margin-left: 10px;
  margin-top: 30px;
}

.back_title {
  position: relative;
  color: #1e5f74;
  top: -14px;
  padding-right: 10px;
  font-size: 20px;
  background-color: #e7e7e7;
}

.back_skills {
  display: flex;
  text-align: center;
  font-family: "Open Sans", sans-serif;
}

.back_skills div {
  margin-left: 20px;
}

.back_skills span {
  margin-left: 20px;
}

.nodejs_logo {
  background-image: url(/portfolio/static/media/nodejs.d5c11bff.png);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100px;
  height: 120px;
}

.mongodb_logo {
  background-image: url(/portfolio/static/media/mongodb.70de740d.png);
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100px;
  height: 120px;
}

.mongoose_logo {
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
  margin-top: 50px;
  width: 100px;
  height: 70px;
}

@media (max-width: 768px) {
  .about_container {
    width: 100%;
  }

  .about_info {
    flex-direction: column;
    align-items: center;
  }

  .about_info img {
    width: 50%;
    height: 50%;
  }

  .about_title {
    font-size: 24px;
  }

  .about_content {
    margin-top: 15px;
  }

  .skill_title {
    font-size: 24px;
  }

  .front_skills {
    flex-wrap: wrap;
  }

  .back_skills {
    flex-wrap: wrap;
  }

  .logos {
    width: 80px;
    height: 100px;
  }

  .mongoose_logo {
    margin-top: 60px;
    height: 40px;
  }
}

@media (max-width: 400px) {
  .about_info img {
    width: 70%;
    height: 70%;
  }
}

.navigation_container {
  width: 80%;
  margin: 0 auto;
  padding: 1% 0;
  font-family: "Ubuntu", sans-serif;
  font-size: 32px;
  font-weight: bold;
  font-style: italic;
  border-bottom: 1px #b9b9b9 solid;
}

.navigation_ul {
  display: flex;
  justify-content: space-around;
}

.navigation_ul li {
  list-style: none;
  cursor: pointer;
}

.navigation_ul li:hover {
  color: #133b5c;
}

.navigation_ul li + li {
  border-left: 1px #b9b9b9 solid;
}

.color_black {
  color: #133b5c;
}

@media (max-width: 768px) {
  .navigation_container {
    width: 100%;
    font-size: 24px;
  }
}

.project_container {
  width: 80%;
  height: 44.5vh;
  margin: 0 auto;
  animation: slide_project 1s;
}

@keyframes slide_project {
  0% {
    transform: translate(-100%, 0);
  }

  100% {
    transform: translate(0, 0);
  }
}

.project {
  width: 40%;
  margin-left: 5%;
  margin-top: 5%;
  cursor: pointer;
  -webkit-filter: grayscale(100%) blur(2px);
  filter: grayscale(100%) blur(2px);
  transition: 0.3s ease-in-out;
  color: #fcdab7;
}

.project:hover {
  -webkit-filter: grayscale(0) blur(0);
  filter: grayscale(0) blur(0);
}

.project_title {
  font-weight: bold;
  font-size: 48px;
  background: linear-gradient(to bottom right, #1d2d50 30%, #1e5f74);
  text-align: center;
  font-family: "Goldman", cursive;
  padding: 10% 0;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  border: 1px #b9b9b9 solid;
}

.project_content {
  font-family: "Open Sans", sans-serif;
  padding: 10% 0;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  border: 1px #b9b9b9 solid;
  border-top: none;
  color: #1e5f74;
}

.project_content span {
  margin: 0px 10px;
}

.project_modalOverlay {
  display: none;
  background-color: rgba(200, 200, 200, 0.486);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
}

.project_modalContainer {
  width: 90%;
  height: 90%;
  background-color: white;
  position: relative;
  top: 5%;
  left: 5%;
  border-radius: 15px;
}

.project_closeBtn {
  font-size: 20px;
  position: relative;
  top: 1%;
  left: 97%;
  cursor: pointer;
  border: none;
  background: none;
}

.project_modal {
  height: 90%;
  text-align: center;
  overflow-y: auto;
}

.image {
  width: 90%;
  height: 100%;
  margin-bottom: 18%;
}

.modal_footer {
  text-align: center;
  background-color: grey;
  height: 30%;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 20px;
}

.modal_footer span {
  display: block;
  position: relative;
  top: 10%;
}

.modal_footer button {
  position: relative;
  top: 20%;
  padding: 8px 15px;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 18px;
  cursor: pointer;
}

.show {
  display: block;
}

.hide {
  display: none;
}

@media (max-width: 768px) {
  .project_container {
    width: 100%;
    height: 60vh;
  }

  .project {
    width: 90%;
    margin-left: 5%;
    margin-top: 5%;
  }

  .project_title {
    font-size: 28px;
  }

  .project_closeBtn {
    top: 2%;
    left: 91%;
  }

  .project_modal {
  }

  .image {
    height: 80%;
  }

  .modal_footer {
    font-size: 16px;
  }

  .modal_footer button {
    font-size: 16px;
    padding: 3px 8px;
  }
}

.contact_container {
  background-image: url(/portfolio/static/media/wall.840dd014.jpg);
  background-size: 100%;
  width: 80%;
  height: 80vh;
  margin: 0 auto;
  animation: slide_contact 1s;
}

@keyframes slide_contact {
  0% {
    transform: translate(100%, 0);
  }

  100% {
    transform: translate(0, 0);
  }
}

.contact_info {
  font-size: 30px;
  font-family: "Open Sans", sans-serif;
  background-color: #fcdab7;
  color: #1d2d50;
  width: 40%;
  height: 50%;
  margin: 0 auto;
  position: relative;
  top: 30%;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.contact_title {
  display: block;
  padding-top: 20px;
  font-weight: bold;
}

.contact_mailIcon {
  display: block;
  margin-top: 30px;
}

.contact_content {
  display: block;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: #1d2d50;
}

.contact_gitIcon {
  display: block;
  margin-top: 20px;
}

.contact_gitContent {
  display: block;
}

@media (max-width: 768px) {
  .contact_container {
    width: 100%;
    height: 58vh;
  }

  .contact_info {
    font-size: 16px;
    width: 75%;
    height: 45%;
  }

  .contact_mailIcon {
    margin-top: 10px;
  }

  .contact_gitIcon {
    margin-top: 5px;
  }
}

.header_container {
  width: 80%;
  margin: 0 auto;
  /* background-color: #1d2d50; */

  background: linear-gradient(to bottom right, #1d2d50 30%, #1e5f74);
  height: 35vh;
  border: 10px solid #fcdab7;
}

.header_content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.header_content img {
  width: auto;
  height: 90%;
  border-radius: 50%;
}

.header_intro {
  font-family: "Open Sans", sans-serif;
  font-size: 24px;
  margin-left: 20px;
}

.header_changingTitle {
  color: #fcdab7;
  font-size: 28px;
  animation: fadeIn 2s infinite;
  display: block;
  font-weight: bold;
}

.header_title {
  color: #fcdab7;
  display: block;
  margin-top: 5px;
}

.hide {
  display: none;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .header_container {
    width: 90%;
    height: 25vh;
  }

  .header_intro {
    font-size: 18px;
  }

  .header_changingTitle {
    font-size: 22px;
  }
}

